import uniStatusBar from "@/components/uni-status-bar/uni-status-bar"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniStatusBar,
		uniLoadMore
	},
	data(){
		return{
			url:'&r=api.home.morecate',
			url2:'&r=api.home.morecate.categoods',
			tapped:'',
			category:[],
			active:1,
			tabActive:0,
			sh:'',
			dataList:[],
			page:1,
			pageSize:20,
			total:0,
			more:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   mounted(){
	   	var that=this
	   	this.getCategory()
		this.getList('','')
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
	   			that.sh=res
	   		})
	   	},0)
	   },
	methods:{
		toggleTab(t,item){
			this.tabActive=t
			this.tapped=item.id
			this.reset()
			this.getList('',item.id)
		},
		to(w){
			uni.navigateTo({
				url:'./goodsList'
			})
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		toggle(t){
			this.active=t
			if(t==1){
				 this.url='&r=api.home.morecate'
				 this.url2='&r=api.home.morecate.categoods'
			}else{
				 this.url='&r=api.home.morecate.brand'
				this.url2='&r=api.home.morecate.brandgoods'
			}
			this.tapped=false
			this.reset()
			this.getCategory()
			this.getList('','')
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		getCategory(){
			var that=this
			  var params={
			  	   id:''
			  }
			  this.$apiPost(this.url,params).then((res) =>{
				  that.category=res.data
				  that.tabActive=0
			  })
		},
		getList(p,id){
			var that=this
			// var params={
			//   page:p,
			//   pagesize: this.pageSize
			// }
			// if(this.page==1){
			// 	this.$loading()
			// }
			  var params={
			  	   id:id
			  }
			  this.$apiPost(this.url2,params).then((res) =>{
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  // if(that.page==1){
				  // 	uni.hideLoading()
				  // }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page,id)
		  // },2000)
		},
	}
}
